import {
  GeistProvider,
} from "@geist-ui/core";
import { useEffect, useRef, useState } from "react";
import { NotSupported } from "./components/NotSupported/NotSupported";
import { Container } from "./components/Container/Container";
import { Header } from "./components/Header/Header";
import { Chat } from "./types";
import { Content } from "./components/Content/Content";
import { Footer } from "./components/Footer/Footer";
import { useDateTimeRag } from "./rag/useDateTimeRag";

const checkAI = async () => {
  if ("ai" in window) {
    if ((await window.ai.assistant.capabilities()).available === "readily") {
      return true;
    }
  }
  return false;
};

export function ChatUI() {
  const rawChatHistory = useRef<Chat[]>([]);
  const [hasAI, setHasAI] = useState<null | boolean>(null);
  const [ragEnabled, setRagEnabled] = useState<boolean>(false);
  const [chatHistory, setChatHistory] = useState(rawChatHistory.current);
  const [inputValue, setInputValue] = useState("");
  const [inferring, setInferring] = useState(false);
  const dateTimeRag = useDateTimeRag();
  const rags = [ dateTimeRag ];

  const [model, setModel] = useState({
    promptStreaming: (inputValue?: string) => {},
  });

  const updateHasAI = async () => {
    const checkAIStatus = await checkAI();

    if (checkAIStatus) {
      const thisModel = await window.ai.assistant.create();
      setModel(thisModel);
    }

    setHasAI(checkAIStatus);
  };

  const updateMessage = (id: number, message: string) => {
    if (id <= rawChatHistory.current.length) {
      rawChatHistory.current[id - 1].message = message;
    }
  };

  const finaliseMessage = (id: number) => {
    if (id <= rawChatHistory.current.length) {
      rawChatHistory.current[id - 1].stream = undefined;
    }
  };

  useEffect(() => {
    updateHasAI();
  }, []);

  const sendPrompt = async () => {
    if (inputValue === "") {
      return;
    }
    setInferring(true);

    rawChatHistory.current.push({
      id: rawChatHistory.current.length + 1,
      role: "user",
      message: inputValue,
    });
    setChatHistory(rawChatHistory.current);
    setInputValue("");

    let ragValue = "";
    if (ragEnabled) {
      const ragsResults = rags.map((rag) => rag.promptChecker(inputValue)).filter(x => x.length > 0);
      if (ragsResults.length > 0) {
        ragsResults.forEach((ragsResult) => ragsResult.forEach((result) => ragValue += `System: ${result}\n`));
        ragValue += `User: ${inputValue}\n`;
        ragValue += `Assistant: `;
        console.log(`[Modified prompt sent]\n${ragValue}`);
      }
    }
    else {
      ragValue += `User: ${inputValue}\n`;
      ragValue += `Assistant: `;
    }
    const modelInput = inputValue;
    const stream = model.promptStreaming(
      ragValue || modelInput
    ) as unknown as ReadableStream;

    rawChatHistory.current.push({
      id: rawChatHistory.current.length + 1,
      role: "assistant",
      message: "",
      stream,
    });
    setChatHistory(rawChatHistory.current);

    setInferring(false);
  };

  return (
    <GeistProvider>
      <NotSupported show={hasAI === false} />
      <Container>
        <Header />
        <Content chatHistory={chatHistory} updateMessage={updateMessage} finaliseMessage={finaliseMessage} />
        <Footer
          rag={rags}
          ragEnabled={ragEnabled}
          setRagEnabled={setRagEnabled}
          hasAI={hasAI}
          inferring={inferring}
          sendPrompt={sendPrompt}
          setInputValue={setInputValue}
          inputValue={inputValue}
        />
      </Container>
    </GeistProvider>
  );
};
