import { useState, useEffect } from "react";
import Message from "./Message";
import { Role } from "../../types";

interface AiStreamMessageProps {
  role: Role;
  stream: ReadableStream;
  id: number;
  updateMessage: (id: number, message: string) => void;
  finaliseMessage: (id: number) => void;
  onChange?: (message?: string) => void;
}

function AiStreamMessage({ role, stream, id, updateMessage, finaliseMessage, onChange }: AiStreamMessageProps) {
  const [message, setMessage] = useState<string>("");
  const [done, setDone] = useState<boolean>(false);
  const finalise = () => {
    setDone(true);
    finaliseMessage(id);
  }

  useEffect(() => {
    function onError(err) {
      console.error("Stream error", err);
      finalise();
    }

    function processReader(reader) {
      reader.read().then(function processText({ done, value }) {
        if (done) {
          reader.cancel();
          finalise();
          return;
        }
        setMessage(value);
        updateMessage(id, value);
        return reader.read().then(processText, onError);
      }, onError);
    }

    try {
      const reader = stream.getReader();
      processReader(reader);
    }
    catch (e) {
      try {
        new Promise((resolve) => setTimeout(resolve, 800)).then(() => {
          const reader = stream.getReader();
          processReader(reader);
        });
      }
      catch (e) {
        setMessage("I'm sorry, I am having some trouble processing this message.");
        finalise();
        return;
      }
    }
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return <Message role={role} message={message} working={!done} onChange={onChange}></Message>;
};

export default AiStreamMessage;
