export function useDateTimeRag() {

  const promptChecker = (prompt: string) => {
    let result = [];

    if (prompt.match(/\btime\b/i)) {
      result.push("It is currently " + new Date().toLocaleTimeString('en-US', {
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
      }));
    }

    if (prompt.match(/\bday\b/i) || prompt.match(/\btoday\b/i)) {
      result.push("Today is " + new Date().toLocaleDateString('en-US', {
        weekday: 'long',
      }));
    }

    if (prompt.match(/\bdate\b/i)) {
      result.push("The date is " + new Date().toLocaleDateString('en-US', {
        weekday: 'long',
        day: 'numeric',
        month: 'long',
        year: 'numeric'
      }));
    }

    if (prompt.match(/\btomorrow\b/i)) {
      result.push("Tomorrow is " + ((new Date((new Date()).getTime() + 24 * 60 * 60 * 1000)).toLocaleDateString('en-US', {
        weekday: 'long',
      })));
    }

    if (prompt.match(/\bChristmas\b/i) || prompt.match(/\bXmas\b/i)) {
      const daysUntilChristmas = () => {
        const now = new Date();
        const currentYear = now.getFullYear();
        let christmas = new Date(currentYear, 11, 25); // December 25th of the current year
      
        // If Christmas has already passed this year, use next year's Christmas
        if (now > christmas) {
          christmas = new Date(currentYear + 1, 11, 25);
        }
      
        const oneDay = 24 * 60 * 60 * 1000; // One day in milliseconds
        const diffDays = Math.round((christmas.getTime() - now.getTime()) / oneDay);
      
        return diffDays;
      };

      result.push(`There are ${daysUntilChristmas()} days until Christmas.`);
    }

    return result;
  };

  return {
    name: "time and dates",
    description: "Provides basic current date and time information.",
    promptChecker
  }
}