import { useState } from "react";
import { RagInfo } from "../RagInfo/RagInfo";
import styles from "./Footer.module.css";

import {
  Button,
  Input,
  Toggle,
} from "@geist-ui/core";

interface FooterProps {
  hasAI: boolean;
  inferring: boolean;
  rag: {
    name: string;
    description: string;
    promptChecker: (prompt: string) => string[];
  }[];
  ragEnabled: boolean;
  setRagEnabled: (ragEnabled: boolean) => void;
  inputValue: string;
  setInputValue: (inputValue: string) => void;
  sendPrompt: () => void;
}

export function Footer({
  rag,
  ragEnabled,
  setRagEnabled,
  hasAI,
  inferring,
  sendPrompt,
  setInputValue,
  inputValue
}: FooterProps) {
  const [ showRagInfo, setShowRagInfo ] = useState<boolean>(false);

  return <>
    <RagInfo visible={showRagInfo} rag={rag} onClose={() => setShowRagInfo(false)} />
    <div className={styles.footer}>
      <div className={styles.toggleRag}>
          <Toggle
            id="enableRag"
            checked={ragEnabled}
            disabled={rag.length === 0}
            type="default"
            onChange={(e) => setRagEnabled(e.target.checked)}
          >
          </Toggle>
          <label htmlFor="enableRag" className={styles.toggleLabel}>
            Enable <a href="#" onClick={() => setShowRagInfo(true)}>{rag.length === 1 ? rag[0].name : 'RAG'}</a>
          </label>
      </div>
      <div className={styles.message}>
        <Input
          width={"100%"}
          scale={4 / 3}
          placeholder="Please input"
          value={inputValue}
          onInput={(e) => {
            if ("value" in e.target) {
              setInputValue(e.target.value as string);
            }
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              sendPrompt();
            }
          }}
          disabled={!hasAI}
          onPointerEnterCapture={undefined}
          onPointerLeaveCapture={undefined}
          crossOrigin={undefined}
        />
      </div>
      <div className={styles.send}>
        <Button
          type="secondary"
          onClick={sendPrompt}
          disabled={!hasAI || inferring}
          placeholder={undefined}
          onPointerEnterCapture={undefined}
          onPointerLeaveCapture={undefined}
        >
          Send
        </Button>
      </div>
    </div>
  </>;
}
