import { useRef } from "react";
import { Chat } from "../../types";
import AiStreamMessage from "../Messages/AiStreamMessage";
import Message from "../Messages/Message";
import styles from "./Content.module.css";

interface ContentProps {
  chatHistory: Chat[];
  updateMessage: (id: number, message: string) => void;
  finaliseMessage: (id: number) => void;
}

export function Content({ chatHistory, updateMessage, finaliseMessage }: ContentProps) {
  const scrollable = useRef<HTMLDivElement>(null);
  const onChange = () => {
    if (scrollable.current) {
      scrollable.current.scrollTop = scrollable.current.scrollHeight;
    }
  };

  return <div className={styles.content} ref={scrollable}>
    {chatHistory.map((chat) => {
      return (
        <div key={chat.id}>
          {chat.role === "user" || !chat.stream ? (
            <Message
              role={chat.role}
              message={chat.message}
              working={false}
              onChange={onChange}
            ></Message>
          ) : (
            <AiStreamMessage
              role={chat.role}
              stream={chat.stream}
              id={chat.id}
              updateMessage={updateMessage}
              finaliseMessage={finaliseMessage}
              onChange={onChange}
            ></AiStreamMessage>
          )}
        </div>
      );
    })} 
  </div>;
}
