import { Modal } from "@geist-ui/core";

interface RagInfoProps {
  visible: boolean;
  onClose: () => void;
  rag: {
    name: string;
    description: string;
    promptChecker: (prompt: string) => string[];
  }[];
}

export function RagInfo(props: RagInfoProps) {
  return <Modal visible={props.visible} onClose={props.onClose}>
    <Modal.Title>RAG installed</Modal.Title>
    <Modal.Content>
      <ul>
        { props.rag.map((rag) => <li key={rag.name}>{rag.description}</li>) }
      </ul>
    </Modal.Content>
  </Modal>;
}
