import { Card, Grid, Loading, Spacer } from "@geist-ui/core";
import { Chrome, User } from "@geist-ui/icons";
import Markdown from "react-markdown";
import { Role } from "../../types";
import { useEffect } from "react";

interface MessageProps {
  role: Role;
  message: string;
  working: boolean;
  onChange?: (message?: string) => void;
}


const Message = ({ role, message, working, onChange }: MessageProps) => {
  useEffect(() => {
    if (onChange) {
      onChange(message);
    }
  }, [ message ]);

  return (
    <Grid.Container justify={role === "assistant" ? "flex-start" : "flex-end"}>
      <Grid xs={20}>
        <Card
          shadow
          type={role === "assistant" ? "dark" : "success"}
          width="100%"
        >
          <Grid.Container>
            <Grid xs={1} style={{ alignItems: "center" }}>
              <div>
                {role === "user" ? <User /> : <Chrome />}
                {working && <Loading type="warning"></Loading>}
              </div>
            </Grid>
            <Grid xs={22} direction="column">
              <Markdown>{message}</Markdown>
            </Grid>
          </Grid.Container>
        </Card>
      </Grid>
      <Grid xs={24}>
        <Spacer h={1}></Spacer>
      </Grid>
    </Grid.Container>
  );
};

export default Message;
